.portrait {
    text-align: center;
    padding: 10px 10px;
    width: 240px;
    height: 240px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    border-radius: 50%;
    background-size: cover;
  }

.name {
    font-variant: normal;
    font-style: normal;
    text-align: center;
    font-size: 1.15em;
    margin-top: 24px;
    margin-bottom: 24px;
}

.affiliation {
    text-align: center;
    font-variant: normal;
    font-style: normal;
    font-size: 0.9em;
    margin-bottom: 8px;
}

ul.network-icon {
    text-align: center;
    padding: 10px 10px;
    position: relative;
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    padding: 0;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
}

.network-icon li {
    text-align: center;
    padding: 1px 1px;
    margin-right: 10px;
  }
  
.network-icon li:last-of-type {
    margin-right: 0;
}
  
.network-icon li:hover {
    transform: scale(1.5)
}

.introduction {
    font-variant: normal;
    font-size: 0.9em;
    text-align: justify;
    margin-right: 80px;
}

.interests {
    font-variant: normal;
    font-style: normal;
    font-size: 0.9em;
    margin-bottom: 100px;
    margin: 0px 0 10px 0;
}

.degree {
    font-variant: normal;
    font-style: normal;
    font-size: 0.9em;
    margin-right: auto;
}

.institute {
    font-size: 0.8em;
    letter-spacing: 0.01em;
    margin: 0;
    font-variant: small-caps;
    margin-right: 10px;
}
