.talks * {
    z-index: 2;
    margin: 8% 5%;
    img {
        padding: 0 0;
        margin: 0 0;
    }

    .talks-item {
        right: 0;
        margin: 0 0 15px;
    }
    .talks-link {
        display: block;
        position: relative;
        margin: 0 auto;
        max-width: 400px;
    }
    .talks-topic {
        font-size: 15px;
        color: #fff;
        margin: auto;
        text-align: left;
        display: inline-block;
        vertical-align: middle;
    }
    .talks-desc {
        color: #808080;
        margin: auto;
        text-align: left;
        font-style: italic;
        display: inline-block;
        vertical-align: middle;
    }
}

/* --- Responsive --- */

@media screen and (max-width: $sm-break) {
    .talks {
        .caption-content {
            font-size: 7px;
        }
    }
}
