.projects * {
    z-index: 2;
    margin: 8% 5%;
    img {
        padding: 0 0;
        margin: 0 0;
    }

    .projects-item {
        right: 0;
        margin: 0 0 15px;

        @media(min-width: 767px) {
            margin: 0 0 15px;
        }

        .projects-link {
            display: block;
            position: relative;
            margin: 0 auto;
            max-width: 400px;
        }
    }
    .project-img {
        text-align: center;
        vertical-align: middle;
        margin: auto;
        position: absolute;
        top: 0; left: 0; right: 0; bottom: 0;
        overflow: auto;

        @media screen and (max-width: $sm-break) {
            font-size: 7px;
        }
    }
    .project-intro {
        font-family: 'Lora', 'Times New Roman', serif;
        margin: auto;
        text-align: left;
        display: inline-block;
        vertical-align: middle;
    }
}

.projects-title {
    font-size: 1.2em;
    text-align: left;
}

/* --- Responsive --- */

@media screen and (max-width: $sm-break) {
    .projects {
        .caption-content {
            font-size: 7px;
        }
    }
}
