* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

figure {
    margin: 0;
}

// For correct line number width in Github Gists
.gist table tbody tr td {
    box-sizing: content-box;
}

html {
    background: $background-color;
}

// Typography
::selection {
    background: $selection-color;
}

::-moz-selection {
    background: $selection-color;
}

body {
    color: $text-color;
    font-family: $font-family-main;
    font-size: $font-size;
    word-wrap: break-word;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $font-family-headings;
    line-height: 1.3;
    margin: 0.67em 0;
    a {
        color: $text-color;
    }
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.4em;
}

h3 {
    font-size: 1.2em;
}

h4 {
    font-size: 1.05em;
}

blockquote {
    border-left: 2px solid darken($blockquote-color, 20%);
    margin: 1em 1em;
    background-color: $blockquote-color;
    padding: 0 0 0 20px;
    display: block;
    font-size: initial;
    font-style: oblique;
}

blockquote p:last-child,
footer p:last-child {
    margin-bottom: 0;
}

table {
    table-layout: fixed;
    width: 100%;
    word-wrap: break-word;
    @media (max-width: 1100px) {
        overflow-x: scroll;
        display: inline-block;
    }
}

td,
th {
    padding: 0.5em 1em;
    border: 1px solid $border-color;
    text-align: left;
}

table,
dl,
kbd,
samp {
    margin: 1em 0;
}

dt {
    font-weight: bold;
}

dd {
    margin-left: 2em;
}

p,
ol,
ul,
dl,
.math-display {
    line-height: 1.5;
    margin-bottom: 0.5em;
}

// KaTeX math display
.math-display {
    display: inline-block;
    width: 100%;
}

// Lists within lists
li {
    p {
        display: inline;
    }
    & > ul,
    & > ol {
        margin-bottom: 0;
        margin-left: 1em;
    }
}

ol,
ul {
    list-style-position: inside;
}

hr {
    border: 0;
    border-top: 1px solid $border-color;
    border-bottom: 1px solid #fff;
    margin: 1em 0;
}

a {
    color: $link-color;
    text-decoration: none;
}

iframe,
img,
embed,
object,
video {
    max-width: 100%;
    padding: 0 10%;
}

img[align=left] {
    margin-right: 3%;
}

img[align=right] {
    margin-left: 3%;
}

.inline-img {
    display: inline;
    margin: 0 0 -3pt 0;
    padding: 0;
    vertical-align: baseline;
}

/* ---- Responsive ---- */

@media screen and (max-width: $sm-break) {
    h1 {
        font-size: 2em;
    }
    h2 {
        font-size: 1.5em;
    }
    h3 {
        font-size: 1em;
    }
    h4 {
        font-size: 0.75em;
    }
    p,
    li {
        font-size: 0.75em;
    }
}

h2::before { 
    display: block; 
    content: " "; 
    margin-top: -100px; 
    height: 100px; 
    visibility: hidden; 
    pointer-events: none;
}

h3::before { 
    display: block; 
    content: " "; 
    margin-top: -100px; 
    height: 100px; 
    visibility: hidden; 
    pointer-events: none;
}

h4::before { 
    display: block; 
    content: " "; 
    margin-top: -100px; 
    height: 100px; 
    visibility: hidden; 
    pointer-events: none;
}

.main {
    margin-bottom: 120px;
}

// introduction grid layout
* {
    box-sizing: border-box;
  }
  
  .container {
    padding: 64px;
  }
  
  /* Clear floats */
  .row:after {
    content: "";
    display: table;
    clear: both
  }
  
  /* 2/3 column */
  .column-62 {
    float: left;
    width: 61.8%;
    padding: 35px;
  }
  
  /* 1/3 column */
  .column-38 {
    float: left;
    width: 38.2%;
    padding: 35px;
  }

  .column-50 {
    float: left;
    width: 50%;
    padding: 25px;
  }

  .column-45 {
    float: left;
    width: 45%;
    padding: 0px;
    margin-top: 10px;
  }

  .column-55 {
    float: left;
    width: 45%;
    padding: 0px;
    margin-top: 10px;
  }

  .column-10 {
    float: left;
    width: 10%;
    padding: 65px;
    align-content: left;
  }

  .column-30 {
    float: left;
    width: 30%;
    padding: 25px;
    align-content: left;
  }

  .column-70 {
    float: left;
    width: 70%;
    padding: 25px;
    align-content: left;
  }

  /* Add responsiveness - make the columns appear on top of each other instead of next to each other on small screens */
  @media screen and (max-width: 1000px) {
    .column-62,
    .column-38 {
      width: 100%;
      text-align: center;
    }
  }

  :root {
    --global-bg-color: #{$white-color};
    --global-code-bg-color: #{$code-bg-color-light};
    --global-text-color: #{$black-color};
    --global-text-color-light: #{$grey-color};
    --global-theme-color: #{$grey-color};
    --global-hover-color: #{$grey-color};
    --global-footer-bg-color: #{$grey-color-dark};
    --global-footer-text-color: #{$grey-color-light};
    --global-footer-link-color: #{$white-color};
    --global-distill-app-color: #{$grey-color};
  
    .fa-sun {
      display : none;
    }
    .fa-moon {
      padding-left: 10px;
      padding-top: 12px;
      display : block;
    }
  }