// Mix-ins
%padding-small {
    padding: $padding-x-small $padding-x-small * 2;
    @media (max-width: 1000px) {
        padding: $padding-x-small $padding-x-small;
    }
}

%padding-regular {
    padding: 1% $padding-large;
    @media (max-width: 1000px) {
        padding: calc($padding-large / 2.5)
    }
    @media (max-width: 576px) {
        padding: $padding-small;
    }
}

.header-txt-shadow {
    text-shadow: 1px 1px 2px $text-shadow;
}

/* unvisited link */
a:link {
    color: $link-color;
}
  
/* visited link */
a:visited {
    color: $link-color-visited;
}

a:hover {
    cursor: pointer;
}


.home-link {
    &:hover {
        text-decoration: underline;
        color: darken($link-color, 15%);
    }
}

.body-link {
    &:hover {
        text-decoration: underline;
        color: darken($link-color, 15%);
    }
}

.post-link {
    &:hover {
        text-decoration: none;
        color: lighten($link-color, 10%);
    }
}

// States
.disabled {
    opacity: 0.7;
}

// Custom
.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
    margin-right: 150px;
}

.right {
    float: right;
}

a, table.table a {
    color: var(--global-theme-color);
    &:hover {
      color: var(--global-theme-color);
      text-decoration: underline;
    }
    &:hover:after :not(.nav-item.dropdown) {
      width: 100%;
    }
  }

  
// Publications

.publications {
    margin-top: 2rem;
    h1 {
      color: var(--global-theme-color);
      font-size: 2rem;
      text-align: center;
      margin-top: 1em;
      margin-bottom: 1em;
    }
    h2 {
      margin-bottom: 1rem;
      span {
        font-size: 1.5rem;
      }
    }
    h2.year {
      color: #314549;
      padding-top: 0rem;
      margin-top: 0rem;
      margin-bottom: 0rem;
      text-align: left;
    }
    ol.bibliography {
      list-style: none;
      padding: 0;
      margin-top: 0;
  
      li {
        margin-bottom: 1rem;
        .abbr {
          height: 2rem;
          margin-bottom: 0.5rem;
          abbr {
            display: inline-block;
            background-color: var(--global-theme-color);
            padding-left: 1rem;
            padding-right: 1rem;
            a {
              color: white;
              &:hover {
                text-decoration: none;
              }
            }
          }
          .award {
            color: var(--global-theme-color) !important;
            border: 1px solid var(--global-theme-color);
          }
        }
        .title {
          font-weight: bolder;
        }
        .author {
          a {
            border-bottom: 1px dashed var(--global-theme-color);
            &:hover {
                border-bottom-style: solid;
                text-decoration: none;
            }
          }
          > em {
            font-style: italic !important;
            font-size-adjust: 0.8;
          }
        }
        .links {
          a.btn {
            color: var(--global-text-color);
            border: 1px solid var(--global-text-color);
            padding-left: 1rem;
            padding-right: 1rem;
            padding-top: 0.25rem;
            padding-bottom: 0.25rem;
            &:hover {
              color: var(--global-theme-color);
              border-color: var(--global-theme-color);
            }
          }
        }
        .hidden {
          font-size: 0.875rem;
          max-height: 0px;
          overflow: hidden;
          text-align: justify;
          -webkit-transition: 0.15s ease;
          -moz-transition: 0.15s ease;
          -ms-transition: 0.15s ease;
          -o-transition: 0.15s ease;
          transition: all 0.15s ease;
  
          p {
            line-height: 1.4em;
            margin: 10px;
          }
          pre {
            font-size: 1em;
            line-height: 1.4em;
            padding: 10px;
          }
        }
        .hidden.open {
          max-height: 100em;
          -webkit-transition: 0.15s ease;
          -moz-transition: 0.15s ease;
          -ms-transition: 0.15s ease;
          -o-transition: 0.15s ease;
          transition: all 0.15s ease;
        }
        div.abstract.hidden {
          border: dashed 1px var(--global-bg-color);
        }
        div.abstract.hidden.open {
          border-color: var(--global-text-color);
        }
      }
    }
  }
  